<script>
import axios from 'axios'
import moment from 'moment'
import { msgError, msgSuccess, tokenApi , apiUrl} from './../../assets/custom/custom.js'
import { async } from 'q'

export default {
    data (){
        return {
            rs_id: '',
            rs_action: '',
            delivery_name: '',
            bo_name: this.$session.get('user_fullname'),
            plate_number: '',
            file: '',
            status: '',
            pictureImage: '',
            loading_status: false,
            isDisabled: false,
            isDisabled2: false,
            isReadonly: false,
            img: '',
            image: './../../assets/user-default.jpeg',
            images: [],
            images2: [],
            img_files: [],
            isfile1: false,
            isfile2: false,
        }
    },
    created() {
        this.rs_id = this.$route.params.rs_id
        this.rs_action = this.$route.params.rs_action
    },
    computed: {
         message(){
             return this.$store.getters.message
         }
    },
    async mounted(){
        await this.getFormDetails()
    },
    methods: {

        convertURIToImageData(URI) {
            return new Promise(function(resolve, reject) {
                if (URI == null) return reject();
                var canvas = document.createElement('canvas'),
                    context = canvas.getContext('2d'),
                    image = new Image();
                image.addEventListener('load', function() {
                canvas.width = image.width;
                canvas.height = image.height;
                context.drawImage(image, 0, 0, canvas.width, canvas.height);
                resolve(context.getImageData(0, 0, canvas.width, canvas.height));
                }, false);
                image.src = URI;
            });
        },

        async uploadimage(){
            var deliversign = ''
           
           //console.log(this.$refs.signature)
          // console.log(this.$refs.signaturePad2)
            var bosign = ''
            var data2 = []
            let fname = []
            var movnum = ''
            let formData = new FormData()
            var { isEmpty, data } = this.$refs.signature.saveSignature()
            //console.log(this.$refs.signature.getPropImagesAndCacheImages())
           //console.log(this.$refs.signaturePad2.getPropImagesAndCacheImages())
            deliversign = data
            console.log("Delsign"+deliversign);

            data2 = this.$refs.signaturePad2.saveSignature()
     
            bosign = Object.values(data2)
            bosign = bosign[1]

            for (var i = 0; i < this.img_files.length; i++) {
                formData.append('file['+ i +']', this.img_files[i][0])

            }

         
            formData.append('rs_id', this.rs_id)
            formData.append('signature', data)
            formData.append('signature2', bosign)
            formData.append('boname', this.bo_name)
            formData.append('deliveryname', this.delivery_name)
            formData.append('movenum', this.$route.params.rs_movno)

         
            try
            {
                let result = await axios.post(apiUrl+'purchaser/inquiry_rs/upload', formData)
            }catch (Exception) {
                this.handdleError(Exception)
            }
           
           
        },

        async onFileChange2(e) {
            this.images2 = []
            let vm = this;
            var selectedFiles = e.target.files;
            for (let i = 0; i < selectedFiles.length; i++) {
              
                this.images2.push(selectedFiles[i]);
            }

            for (let i = 0; i < this.images2.length; i++) {
                let reader = new FileReader();
                reader.onload = (e) => {
                this.$refs.image2[i].src = reader.result;
                };

                reader.readAsDataURL(this.images2[i]);
            }
            
            //this.img_files = this.$refs.upload_images2.files;
            this.img_files.push(this.$refs.upload_images2.files)
            console.log(this.img_files)
        },

        async onFileChange(e) {
            this.images = []
            let vm = this;
            var selectedFiles = e.target.files;
            for (let i = 0; i < selectedFiles.length; i++) {
              
                this.images.push(selectedFiles[i]);
            }

            for (let i = 0; i < this.images.length; i++) {
                let reader = new FileReader();
                reader.onload = (e) => {
                this.$refs.image[i].src = reader.result;
                };

                reader.readAsDataURL(this.images[i]);
            }
            
            this.img_files.push(this.$refs.upload_images.files)
            console.log(this.img_files)
        },

        async undo() {
            this.$refs.signature.clearSignature();
        },

        async undo2() {
            this.$refs.signaturePad2.clearSignature();
        },

        async handdleError(error=null) {
            if (error != null) {
                const response = JSON.parse(JSON.stringify(error))
                const status = response.response.status.toString()
                const message = (response.response.data.error === undefined) ? response.response.data.message : response.response.data.error.message
                msgError(status, message)
                return true
            }
                msgError(401, "Something Wrong")
        },
        async redirectPrevious() {
            this.$router.push({ name: 'RsInquiry' })
        },
        async handleFileUpload() {
            this.file = this.$refs.file.files[0];
        },
        async submitForm() {
            this.isDisabled = true
            this.isDisabled2 = true
            let formData = new FormData()
            // if (this.file == "") {
            //     return msgError(401, "Invoice file is required")
            // } else 
            if(this.delivery_name == "") {
                return msgError(401, "Delivery name is required")
            } else if(this.plate_number == "") {
                return msgError(401, "Plate number is required")
            }else if(this.$refs.signaturePad2.isEmpty()) {
                return msgError(401, "BO signature is required")
            }else if(this.$refs.signature.isEmpty()){
                return msgError(401, "Delivery signature is required")
            }else if(this.$refs.upload_images.value == ""){
                return msgError(401, "Picture Delivery Man with ID is required")
            }else if(this.$refs.upload_images2.value == ""){
                return msgError(401, "Picture Item is required")
            }

            formData.append('rs_id', this.rs_id)
            formData.append('rs_action', this.rs_action)
            // formData.append('file', this.file)
            formData.append('deliveryName', this.delivery_name)
            formData.append('plateNumber', this.plate_number)
            formData.append('user_id', this.$session.get('user_id'))
            
            try
            {
                let result = await axios.post(apiUrl+'purchaser/inquiry_rs/pickUpItem', formData)

                this.uploadimage()
                this.loading_status = true                
                let data = result.data
                this.status = data.status
                this.isDisabled = false
                this.isDisabled2 = false
                this.$router.push({ name: 'RsInquiry' })
                msgSuccess(200, "Save Successful")
            }catch (Exception) {
                this.handdleError(Exception)
            }
        },
        async getFormDetails() {
            let query = { 
                params: {
                    rs_id: this.$route.params.rs_id
                },
                headers: tokenApi('token', 'header')
            }
            if(this.$route.params.rs_action != '') {
                let result = await axios.get(apiUrl+'purchaser/inquiry_rs/viewItem', query)               
                if(result) {
                    if(result.data.picture == '') {
                        let data = result.data
                        // this.pictureImage = data.picture
                        this.delivery_name = data.deliveryName
                        this.plate_number = data.plateNumber
                    }
                    else
                    {
                        let data = result.data
                        // this.pictureImage = data.picture
                        this.delivery_name = data.deliveryName
                        this.plate_number = data.plateNumber
                        this.isReadonly = true
                        this.isDisabled2 = true
                    }
                }
            }
        }
    }
}

